/*
////////////////////////////////////////////////////////////////////////////
Embed custom fonts here
////////////////////////////////////////////////////////////////////////////
*/


@charset 'UTF-8';

// @font-face mixin

@mixin font-face($family-name, $file-name, $font-weight: normal, $font-style: normal) {
  @font-face {
    $font-path: '../fonts/#{$file-name}';
    font-family: '#{$family-name}';
    src: local('☺'), url($font-path+'.woff') format('woff'),
      url($font-path+'.woff2') format('woff2'), url($font-path+'.eot?iefix') format('eot');
    font-weight: $font-weight;
    font-style: $font-style;
  }
}

@include font-face(
  $family-name: Akkurat Mono,
  $file-name: akkurat-mono,
  $font-weight: 400,
  $font-style: normal
);

// Stanley
// Note: embedding these without the mixin becuase the foundry (Optimo) licenses webfonts with encoded WOFFs. EOTs and SVGs are linked.

@font-face {
    font-family: 'Stanley';
    src: url('StanleyRegular-Regular.eot'); /* IE9 Compat Modes */
    src: url('StanleyRegular-Regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
         url(data:application/font-woff;charset=utf-8;base64,) format('woff'), /* Modern Browsers */
         url('StanleyRegular-Regular.svg#StanleyRegular') format('svg'); /* Legacy iOS */
    font-style: normal;
    font-weight: 400;
}
